.about{
    background-color: #f8f8f8;
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    width: 100%;
}

.about .container{
    width: 1240px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.about h2{
    font-size: 3rem;
}
.about .col-2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem;
}

.about .col-2 p:first-of-type{
    margin: 2rem 0;
    font-size: 1.6rem;
}

.about .col-2 p:nth-child(4){
    font-size: 1.45rem;
}

@media screen and (max-width: 940px) {
    .about .container{
        grid-template-columns: 1fr;
    }

    .about .container{
        width: 100%;
    }

    .about .container img{
        padding: 1rem;
        width: 90vw;
    }

    .about .container .col-2{
        padding: 0;
    }
}